<template>
  <div class="makeInfo_wapper">
    <common-top-title>
      <span slot="mySlot">小提示🍉</span>
    </common-top-title>
    <div class="back_div">
      <p class="tips tips_main">&nbsp;&nbsp;&nbsp;&nbsp;经过ant的升级,现支持两种方式生成朋友圈,两者您可以都试试,选择效果最好的: </p>
      <!-- 如果是苹果手机，建议是第一种 -->
      <p class="tips_ip" v-if="whichPhone&&whichPhone==='pg'">
        🙋:检测到您为ipone用户,建议您使用第一种
      </p>
      <p class="tips"><span class="tips_main">一.</span> 您自己发一条朋友圈(可以设置成'仅自己可看'),然后点进详情将内容部分截图,如图：</p>
      <!-- 示例图 -->
      <div class="inner_show_example common_back"></div>
      <p class="tips">剩下的点赞/评论区我会帮你生成,并将你的截图和这部分结合到一起,非常完美</p>
      <div class="btn_wapper">
        <mt-button type="primary" size="small" @click="choseWay('two')">去试试☝️</mt-button>
      </div>
      <!-- 分割线 -->
      <!-- <div class="fg_line"></div> -->
      <!-- 分割线 end -->
    </div>
    <!-- 第二种方式 -->
    <div class="back_div back_div_se">
      <p class="tips"><span class="tips_main">二. </span>还是原来的方式,生成的朋友圈任何内容都需要您手填</p>
      <div class="btn_wapper">
        <mt-button type="primary" size="small" @click="choseWay('base')">去试试✌️</mt-button>
      </div>
      <!-- 第二种方式 end-->
    </div>
    <div class="back_div back_div_se bottom_tips">
      <span>若遇页面卡顿或其他问题,您可联系我</span>
      <!-- <p>您可长按复制链接在浏览器打开:http://1t.click/jEa</p> -->
      <p class="oldBb">如果您需要旧版,您可点击<a href="https://an888.net/allMyFun/likeWechatOld/#/">旧版本</a></p>
      <!-- <mt-field type="input" label="链接:" v-model="dl"></mt-field> -->
      <div class="xt_flex xt_flex_row_center">
        <!-- 显示二维码 -->
        <div class="bot_rig">
          <span>联系我📟</span>
          <div class="img_skm">
            <img src="../assets/gzh.jpg" height="464" width="466">
          </div>
        </div>
        <!-- 显示二维码 end-->
        <!-- 打赏码 -->
        <div class="bot_left">
          <span>打赏我 💝</span>
          <div class="img_skm">
            <img src="../assets/skm.png" height="464" width="466">
          </div>
        </div>
        <!-- 打赏码 end-->
      </div>
    </div>
    <!-- <comm-bottom></comm-bottom> -->
  </div>
</template>
<script>
import { Indicator } from 'mint-ui';
import { getPhone } from "@/utils"
import commonTopTitle from '@/components/commonTopTitle'
import commBottom from '@/components/commonBottom'
export default {
  components: {
    commonTopTitle,
    commBottom
  },
  data() {
    return {
      lddd: '',
      dl: 'http://1t.click/jEa',
      whichPhone: '' //手机型号
    }
  },
  mounted() {
    // 拿到手机型号
    this.whichPhone = getPhone()
  },
  methods: {
    loadding() {
      Indicator.open({
        text: '如若卡顿,请耐心等待或重试💓',
        spinnerType: 'fading-circle'
      });
    },
    closeLodding() {
      Indicator.close();
    },
    choseWay(type) { //去试试 跳转
      // loadding 针对ipone卡顿
      this.loadding()
      this.$router.push('/wxDetail?type=' + type)
    }
  },
  beforeDestroy: function() {
    let _this = this
    setTimeout(() => {
      _this.closeLodding()
    }, 300)
  }
};

</script>
<style scoped lang="scss">
.makeInfo_wapper {
  padding: 20px;
  overflow: auto;
  position: fixed;
  padding-bottom: 50px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-image: url('../assets/maldives_wedding-wallpaper-1280x800.jpg');

  .inner_show_example {
    width: 100%;
    height: 400px;
    background-image: url('../assets/pyq.jpg');
    background-size: contain;
    border: 1px solid #f5a623;

  }

  .btn_wapper {
    text-align: center;
  }

  .tips {
    color: #009688;
    font-size: 38px;
  }

  .fg_line {
    text-align: center;
    margin: 20px 0;
    width: 100%;
    height: 3px;
    background-color: #333;
  }

  .back_div {
    background-color: #fff;
    opacity: .9;
    padding: 20px 15px;
    border-radius: 10px;

  }

  .tips_main {
    color: #f5a623;
    font-weight: bold;
  }

  .back_div_se {
    margin-top: 25px;
  }

  .bottom_tips {
    text-align: center;

    span {
      color: #f5a623;

    }
  }

  .img_skm {
    width: 150px;
    height: 152px;

    img {
      width: 100%;
    }

    // background-image: url("../assets/skm.png");
  }

  .bot_left {
  display:none;
    margin-left: 20px;
    // text-align: left;
  }
}

.tips_ip {
  color: #f5a623;
  text-align: center;
  font-weight: bold;
}

.oldBb {
  display:none;
  color: #f5a623;

  a {
    color: #26a2ff;
  }
}

</style>



